.progressBar {
    height: 14px;
    width: 100%;
    position: relative;
    border-radius: 20px;
    overflow: hidden;
    background-color: #eaeaea;
}
.progressBar:after {
    left: 40%;
}
.progressBar:before {
    left: 20%;
}

.u-isVisuallyHidden {
    overflow: hidden!important;
    width: 1px!important;
    height: 1px!important;
    margin: -1px!important;
    border: 0!important;
    padding: 0!important;
    position: absolute!important;
    clip: rect(1px 1px 1px 1px)!important;
    clip: rect(1px,1px,1px,1px)!important;
    -webkit-clip-path: rect(1px,1px,1px,1px)!important;
    clip-path: rect(1px,1px,1px,1px)!important;
}

.progressBar-range_1\/5 {
    width: 20%;
}
.progressBar-range_2\/5 {
    width: 40%;
}
.progressBar-range_3\/5 {
    width: 60%;
}
.progressBar-range_4\/5 {
    width: 80%;
}
.progressBar-range_5\/5 {
    width: 100%;
}


.progressBar-range {
    display: inline-block;
    height: 100%;
    position: relative;
    border-radius: 20px 0 0 20px;
    background-color: #6504b5;
    -webkit-transition: width .4s ease-in-out;
    transition: width .4s ease-in-out;
}

.progressBar-inner:after, .progressBar-inner:before, .progressBar:after, .progressBar:before {
    content: "";
    width: 1px;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 1;
    background-color: #fff;
}
.progressBar-inner:after {
    left: 80%;
}
.progressBar-inner:before {
    left: 60%;
}
